import React from "react";
import '../../resource/css/style.scss';
import Label from "../atoms/Label";
import Link from "../atoms/Link";

const AboutContainer = (props) => {
    const { } = props;

    // 
    const onClickTop = () => {
        window.scrollTo({ top: 0, behavior: "smooth", });
    }

    return (
        <div className="about">
            <div className="chapter">
                <div className="chapterTitle">
                    <Label label="" />
                </div>
                <div>
                    <Label
                        className="contents"
                        label={"NewsAPI"
                            + "\nReact"}
                    />
                    <div style={{ display: "flex", margin: "10px 0px 0px 0px" }}>
                        <Label label="NewsApi" />
                        <Link
                            uri="https://newsapi.org/"
                            label="https://newsapi.org/"
                            target="_blank"
                        />
                    </div>
                </div>
            </div>
            <div className="chapter">
                <div className="chapterTitle2">
                    <Label label="" />
                </div>
                <div>
                    <Label
                        className="contents"
                        label={"NewsAPI1100"
                            + "\n "}
                    />
                </div>
            </div>
            <div className="chapter">
                <div className="chapterTitle2">
                    <Label label="" />
                </div>
                <div className="sectionWrap">
                    <Label
                        className="section"
                        label={"Visual Studio Code"}
                    />
                    <Label
                        className="contents"
                        label={""}
                    />
                </div>
                <div className="sectionWrap">
                    <Label
                        className="section"
                        label={"HTML"}
                    />
                    <Label
                        className="contents"
                        label={"Web"}
                    />
                </div>
                <div className="sectionWrap">
                    <Label
                        className="section"
                        label={"JavaScript"}
                    />
                    <Label
                        className="contents"
                        label={""}
                    />
                </div>
                <div className="sectionWrap">
                    <Label
                        className="section"
                        label={"React"}
                    />
                    <Label
                        className="contents"
                        label={"UIJavaScript"}
                    />
                </div>
                <div className="sectionWrap">
                    <Label
                        className="section"
                        label={"CSS"}
                    />
                    <Label
                        className="contents"
                        label={""}
                    />
                </div>
                <div className="sectionWrap">
                    <Label
                        className="section"
                        label={"Redux"}
                    />
                    <Label
                        className="contents"
                        label={""}
                    />
                </div>
                <div className="sectionWrap">
                    <Label
                        className="section"
                        label={"GitHub"}
                    />
                    <Label
                        className="contents"
                        label={""}
                    />
                </div>
            </div>
            <Label
                wrapClassName={"scrollToTopArea"}
                className="scrollToTopBlack"
                label="Scroll To Top"
                onClick={onClickTop}
            />
        </div >
    )
}


export default AboutContainer;